<template>

  <h3>{{ id ? "修改活动" : "发布活动" }}</h3>

  <el-row v-if="!id">
    <el-col :span="6"><strong>注意</strong></el-col>
    <el-col :span="18">
      活动一经发布，活动名称、所在校区及活动地点将<strong>无法</strong>修改，已有的工作岗位及时段将<strong>无法</strong>删除或者修改其名称或时间。
    </el-col>
  </el-row>

  <el-row>
    <el-col :span="24"><strong>基本信息</strong></el-col>
    <el-col :span="0" />
  </el-row>

  <el-row>
    <el-col :span="6">名称</el-col>
    <el-col :span="18"><el-input v-model="v.title" :disabled="Boolean(id)" /></el-col>
  </el-row>
  <el-row>
    <el-col :span="6">校区</el-col>
    <el-col :span="18">
      <el-radio-group v-model="v.campus" :disabled="Boolean(id)">
        <el-radio v-for="(v, i) in campuses" :key="i" :label="i">{{v}}</el-radio>
      </el-radio-group>
    </el-col>
  </el-row>
  <el-row>
    <el-col :span="6">地点</el-col>
    <el-col :span="18"><el-input v-model="v.site" :disabled="Boolean(id)" /></el-col>
  </el-row>

  <el-row>
    <el-col :span="6">简介</el-col>
    <el-col :span="18"><el-input type="textarea" v-model="v.desc" :autosize="{ minRows: 4, maxRows: 8 }" /></el-col>
  </el-row>
  <el-row>
    <el-col :span="6">报名截止时间</el-col>
    <el-col :span="18"><el-date-picker type="datetime" v-model="v.due" /></el-col>
  </el-row>

  <el-row>
    <el-col :span="24"><strong>工作岗位及时段</strong></el-col>
    <el-col :span="0" />
  </el-row>

  <el-collapse v-model="collapseActiveNames">
    <el-collapse-item v-for="(u, i) in v.periods" :key="u.start"
      :title="`时段 ${i + 1}${u.name ? `：${u.name}` : ''}（需 ${u.need} 人）`" :name="i">
      <el-row>
        <el-col :span="6">岗位名</el-col>
        <el-col :span="18"><el-input v-model="u.name" :disabled="i < periodsCount" /></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">人数</el-col>
        <el-col :span="18">
          <el-input-number v-model="u.need" :min="1" />
          <el-button v-if="i >= periodsCount" class="ml"
            type="danger" @click="deletePeriod(i)"> - </el-button>
          <el-button type="primary" class="ml" @click="copyPeriod(i)"> + </el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">日期</el-col>
        <el-col :span="18"><el-date-picker type="date" v-model="u.date" arrow-control /></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">时间</el-col>
        <el-col :span="18">
          <el-time-picker v-model="u.time" format="HH:mm" :disabled="i < periodsCount"
            is-range arrow-control :disabled-hours="disabledHours" />
        </el-col>
      </el-row>
    </el-collapse-item>
  </el-collapse>

  <el-row v-if="!id">
    <el-col :span="24">
        <el-button type="primary" @click="addPeriod">添加时段</el-button>
        <el-popconfirm title="您确定输入的信息准确无误吗？" @confirm="update">
          <template #reference>
            <el-button type="primary" :disabled="!canUpdate">发布活动</el-button>
          </template>
        </el-popconfirm>
    </el-col><el-col :span="0" />
  </el-row>

  <el-row v-else>
    <el-col :span="24">
      <el-button type="primary" @click="addPeriod">添加时段</el-button>
      <el-button type="primary" @click="update" :disabled="!canUpdate">修改活动</el-button>
      <el-popconfirm title="确定要删除该活动吗？" @confirm="deleteActivity">
        <template #reference>
          <el-button type="danger">删除活动</el-button>
        </template>
      </el-popconfirm>
    </el-col><el-col :span="0" />
  </el-row>

</template>

<script>
import AV from 'leancloud-storage'
let dayjs = require('dayjs')
export default {
  name: 'Edit',
  data () {
    let id = this.$route.params.id
    return {
      campuses: ['西土城', '沙河', '宏福'],
      collapseActiveNames: [],
      periodsCount: 0,
      id: id,
      v: {
        title: '',
        campus: AV.User.current().get('campus'),
        site: '',
        desc: '',
        due: undefined,
        periods: [],
      }
    }
  },
  computed: {
    canUpdate () {
      return this.v.title && (this.v.campus !== undefined) && this.v.site && this.v.due && this.v.periods.length 
    },
  },
  beforeMount () {
    if (!this.id) return
    const query = new AV.Query('Activity')
    return query.get(this.id).then(res => {
      this.v = res.toJSON()
      this.periodsCount = this.v.periods.length
    })
  },
  methods: {
    disabledHours () {
      return [0, 1, 2, 3, 4, 5, 22, 23]
    },
    addPeriod () {
      this.v.periods.push({
        date: new Date(),
        time: [
          dayjs().hour(12).minute(0).second(0).toDate(),
          dayjs().hour(12).minute(0).second(0).toDate()
        ],
        name: '',
        need: 10
      })
      this.collapseActiveNames.push(this.v.periods.length - 1)
    },
    deletePeriod (i) {
      this.v.periods.splice(i, 1)
    },
    copyPeriod (i) {
      this.v.periods.push({...this.v.periods[i]})
      this.collapseActiveNames.push(this.v.periods.length - 1)
    },
    update () {
      let activity

      if (!this.id) {
        activity = new AV.Object('Activity')
        activity.set('title', this.v.title)
        activity.set('campus', this.v.campus)
        activity.set('site', this.v.site)
        activity.set('from', AV.User.current())
      } else {
        activity = AV.Object.createWithoutData('Activity', this.id)
      }

      activity.set('maxNeed', this.v.periods.reduce((i, j) => i + j.need, 0))
      activity.set('desc', this.v.desc)
      activity.set('due', dayjs(this.v.due).toDate())
      activity.set('periods', this.v.periods)

      activity.save().then(() => {
        this.$message({ message: `${this.id ? '修改' : '发布'}活动成功。`, type: 'success' })
        this.$router.push('/')
      }).catch(e => this.$store.commit('catchError', e))
    },
    deleteActivity () {
      if (!this.id) return
      let activity = AV.Object.createWithoutData('Activity', this.id)
      let query = new AV.Query('UserActivityMap')
      query.equalTo('activity', activity).find().then(results => {
        return AV.Object.destroyAll(results)
      }).then(() => {
        return activity.destroy()
      }).then(() => {
        this.$message({ message: "删除活动成功。", type: 'success' })
        this.$router.push('/')
      }).catch(e => this.$store.commit('catchError', e))
    }
  }
}
</script>

<style scoped>
.el-row {
  margin: 5px 0;

}
.el-row .el-col:first-child {
  text-align: center;
  line-height: 32px;
}
.el-row .el-col:last-child {
  text-align: left;
}
.ml {
  margin-left: 12px;
}
</style>
